.formulario-diagnostico-container {
    background-color: whitesmoke;
    padding: 20px;
    max-width: 400px;
    margin: 10px auto;
    text-align: left;
  }
  
  .form-group {
    margin-bottom: 15px;
  }
  
  .form-group label {
    display: block;
    margin-bottom: 5px;
    font-weight: bold;
  }
  
  .form-group input {
    width: 100%;
    padding: 8px;
    border-radius: 4px;
    border: 1px solid #ccc;
  }
  
  .form-actions {
    display: flex;
    justify-content: space-between;
  }
  
  .form-actions button {
    padding: 10px 15px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    padding:10px 15px;
    margin-left: 15px;
  }
  
  .form-actions .crear-button {
    background-color: #004d40;
    color: white;
  }
  
  .form-actions .cancel-button {
    background-color: #dc3545;
    color: white;
  }
  .titulo-formulario {
    font-size: 24px;
    text-align: center;
    color: #004d40;
  }
  .boton-crear {
    background-color: #004d40;
    color: white;
    padding: 10px 15px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
  }
  
  .boton-cancelar {
    background-color: #dc3545;
    color: white;
    padding: 10px 15px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
  }
  
  .boton-crear:hover, .boton-cancelar:hover {
    opacity: 0.8;
  }
  